.services {
  background-image: url("../img/fon-blue.jpg");
  text-align: center;
  padding: 110px 0;
  margin-top: 50px;
  &__blocksWrap {
    display: flex;
    justify-content: space-between;
    span {
      padding: 20px;
      border: 2px solid #ffffff;
      border-radius: 50%;
      width: 180px;
      height: 180px;
      display: flex;
      justify-content: center;
      align-items: center;
      box-shadow: 0 0 7px #777777;
      img {
        transition-property: 'transform';
      }
    }
    &--mind img {
      width: 84px;
      height: 98px;
    }
    &--heard img {
      width: 120px;
      height: 80px;
    }
    &--bone img {
      width: 40px;
      height: 125px;
    }
    &--lungs img {
      width: 80px;
      height: 93px;
    }
  }
  &__wrap {
    &> h2 {
        font-size: 48px;
        font-family: $secondFont;
        color: $ServiceColorTitle;
        padding-bottom: 15px;
        text-transform: uppercase;
        font-weight: bold;
    }
    &> h3 {
        font-size: 18px;
        font-family: $mainFont;
        color: $ServiceColorDes;
        padding-bottom: 57px;
        text-transform: uppercase;
    }
  }
  .servicesBlock {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .servicesBlock__text {
    font-size: 25px;
    font-weight: bold;
    color: #ffffff;
    font-family: $secondFont;
    padding-top: 30px;
  }
}
