.ourTeam {
    text-align: center;
    &__wrap {
        padding: 110px 0;
        &> h2 {
            font-size: 48px;
            font-family: $secondFont;
            color: $TeamColorTitle;
            padding-bottom: 15px;
            text-transform: uppercase;
            font-weight: bold;
        }
        &> h3 {
            font-size: 20px;
            font-family: $mainFont;
            color: $TeamColorDes;
            padding-bottom: 57px;
            text-transform: uppercase;
        }
    }
    &__wrapBlocks {
        width: 100%;
        display: flex;
        justify-content: space-between;
        box-sizing: border-box;
    }
    &__Block {
        margin: 0;
        padding: 0;
        &:hover {
            img {
                opacity: 1;
            }
            .ourTeam__Block__text {
                background-image: url("../img/fon-blue.jpg");
            }
        }
        &__text {
            width: 214px;
            height: 214px;
            padding: 10px;
            overflow: hidden;
            background-image: url("../img/fon-black.jpg");
            text-align: left;
            h5 {
                font-size: 22px;
                font-family: $mainFont;
                font-weight: bold;
                color: #ffffff;
                padding-bottom: 10px;
            }
            p {
                font-size: 13px;
                font-family: $secondFont;
                color: #ffffff;
                line-height: 23px;
            }
        }
        &__img {
            width: 234px;
            height: 234px;
            img {
                width: 234px;
                height: 234px;
                opacity: 0.7;
            }
        }
    }
}