.contact {
    background-image: url("../img/map.jpg");
    background-size: 100% 100%;
    background-repeat: no-repeat;
    background-position: left right;
    padding: 130px 0;
    .contactWrap {
        background-image: url("../img/fon-blue.jpg");
        padding: 40px 0;
        h3 {
            font-family: $secondFont;
            border-bottom: 1px solid #ffffff;
            color: #ffffff;
            font-size: 24px;
            width: 100%;
            padding-top: 20px;
            margin-bottom: 20px;
            padding-bottom: 10px;
        }
        &__blocks {
            &--wrap {
                padding: 20px;
                display: flex;
                justify-content: space-between;
            }
        }
        &__info {
            display: flex;
            flex-direction: column;
            width: 45%;
            ul {
                li {
                    display: flex;
                    height: 45px;
                    align-items: center;
                    color: #ffffff;
                    font-family: $secondFont;
                    font-size: 16px;
                    span {
                        width: 30px;
                        height: 30px;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        img {
                            display: block;
                        }
                    }
                    p {
                        padding-left: 10px;
                    }
                }
            }
        }
        &__form {
            width: 45%;
            form {
                display: flex;
                flex-direction: column;
                align-items: flex-end;
                input {
                    height: 34px;
                    border: none;
                    margin-bottom: 10px;
                    padding: 0 2%;
                    font-family: $secondFont;
                    width: 96%;
                }
                textarea {
                    height: 100px;
                    padding: 10px 2%;
                    width: 96%;
                    border: none;
                    resize: none;
                }
                input[type=submit] {
                    display: inline-block;
                    width: 170px;
                    height: 40px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    background-image: url("../img/fon-black.jpg");
                    color: #ffffff;
                    font-size: 18px;
                    cursor: pointer;
                    text-transform: uppercase;
                    margin-top: 30px;
                }
            }
        }
        h2 {
            text-align: center;
            font-family: $mainFont;
            font-size: 50px;
            color: #ffffff;
            font-weight: bold;
            text-transform: uppercase;
        }
    }
}