$blockWidth: 100%;
$btnTopPosition: 45%;
$heightBtn: 50px;
$widthBtn: 50px;

.sliderText {
    margin: 0 auto;
    padding: 0;
    width: $blockWidth; 
    height: 100vh;
    overflow: hidden;
    display: flex;
    position: relative;
    box-sizing: border-box;
    align-items: center;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;
    position: relative;
    flex-direction: column;

    &__mask {
        width: 100%;
        height: $topMenuHeight;
        background-color: #ffffff;
    }

    &__wrapSliders {
        width: 100%;
        height: 100%;
        position: relative;
        top: 0;
        left: 0;
        height: $sliderHeight;
    }

    &__block {
        position: absolute;
        top: 0;
        height: 100vh;
        width: 100%;
        height: 100%;
        font-family: 'Open Sans', sans-serif;
        z-index: 10;
        img {
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            z-index: 10;
        }
    }

    &__text {
        display: block;
        background-color: rgba(16, 28, 50, 0.2);
        font-size: 27px;
        position: absolute;
        bottom: 80px;
        left: 100px;
        z-index: 20;
        padding: 25px;
        border-radius: 5px;
        box-shadow: 0 0 10px #444444;
        transition: background-color 0.2s;
        h5 {
            font-family: $mainFont;
            font-size: 55px;
            color: #ffffff;
            text-transform: uppercase;
            font-weight: bold;
            padding-bottom: 10px;
        }
        h6 {
            font-family: $mainFont;
            font-size: 20px;
            color: #ffffff;
            text-transform: uppercase;
            font-weight: bold;
        }
        button {
            font-family: $secondFont;
            font-size: 18px;
            color: #ffffff;
            width: 160px;
            border: none;
            height: 45px;
            display: flex;
            justify-content: center;
            align-items: center;
            background-image: url("../img/fon-blue.jpg");
            text-transform: uppercase;
            font-weight: bold;
            margin-top: 20px;
            &:hover {
                cursor: pointer;
                opacity: 0.8;
            }
        }
        &:hover {
            background-color: rgba(16, 28, 50, 0.4);
        }
    }
    
    &__wrapBtn {
        display: flex;
        position: absolute;
        top: $btnTopPosition;
        left: 0.5%;
        width: 98%;
        justify-content: space-between;
        &> div {
            width: $widthBtn;
            height: $heightBtn;
            background-color: none;
            color: #ffffff;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 16px;
            font-family: 'Open Sans', sans-serif;
            cursor: pointer;
            opacity: 0;
            transition: all 0.2s;
            position: relative;
            top: 0;
            outline: none;
            img {
                width: 35px;
                height: 35px;
                background-color: none;
            }
            &:last-child img {
                transform: rotate(180deg)
            }
        }
    }
    &:hover .sliderText__wrapBtn {
        opacity: 0.7;
    }
    &:hover .sliderText__wrapBtn:hover {
        opacity: 1;
    }

}



