footer {
    background-color: #ffffff;
    height: 60px;
    display: flex;
    align-items: center;
    font-family: $mainFont;
    color: #a3a3a3;
    font-size: 16px;
    margin: 0;
    padding: 0;
    .footerText {
        display: flex;
        justify-content: space-between;
        &__text {
            display: flex;
            align-items: center;
        }
        &__social {
            display: flex;
            align-items: center;
            justify-content: flex-end;
            flex-direction: row;
        }
        &__social > div{
            display: flex;
            align-items: center;
            justify-content: flex-end;
            margin-left: 20px;
            img {
                height: 30px;
                width: 30px;
                opacity: 0.7;
                transition: all 0.2s;
                cursor: pointer;
                &:hover {
                    opacity: 1;
                }
            }
        }
    }
}