.nav {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;
    background-color: #fff;
    width: 100%;
    box-shadow: 0 0 10px #777777;
    .nav__wrap {
        width: 100%;
        height: $topMenuHeight;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .nav__logo {
            display: flex;
            .nav__logo-img {
                display: flex;
                align-items: center;
                img {
                    width: 44px;
                    height: 50px;
                    display: block;
                }
            }
            .nav__logo-text {
                margin-left: 8px;
                p {
                    color: #383838;
                    font-size: 40px;
                    font-family: $mainFont;
                    font-weight: bold;
                }
                span {
                    color: #383838;
                    font-size: 11px;
                    font-family: $secondFont;
                    font-weight: bold;
                }
            }
        }
        .nav__menu {
            ul {
                display: flex;
                li {
                    padding-left: 24px;
                    transition: all 0.2s;
                    &:first-child {
                        padding: 0;
                    }
                    a {
                        color: $colorNavigation;
                        font-size: 18px;
                        font-family: $mainFont;
                        text-transform: uppercase;
                        &:hover {
                            color: $colorNavigationHover
                        }
                    }
                }
            }
        }
    }
}