$romb: 70px;
$rombColor: #ffffff;
$rombColorText: #42b3e5;
$rombColorHoverText: #737373;
$borderRadiusBlock: 3px;

.about {
    text-align: center;
    padding: 110px 0 50px;
    &> h2 {
        font-size: 48px;
        font-family: $secondFont;
        color: $colorTitle-1;
        padding-bottom: 15px;
        text-transform: uppercase;
        font-weight: bold;
    }
    &> h3 {
        font-size: 20px;
        font-family: $mainFont;
        color: $colorDes-1;
        padding-bottom: 57px;
        text-transform: uppercase;
    }
    &__wrapBlocks {
        display: flex;
        justify-content: space-between;
    }
    &__blocks {
        width: 360px;
        border-radius: $borderRadiusBlock;
        box-shadow: 0 0 7px #ccc;
        position: relative;
        img {
            width: 360px;
            height: 320px;
            opacity: 0.7;
            border-top-left-radius: $borderRadiusBlock;
            border-top-right-radius: $borderRadiusBlock;
        }
        &:hover img {
            opacity: 0.9;
        }
        &:hover .about__description {
            background-image: url("../img/fon-black.jpg");
        }
        &:hover .about__romb span {
            color: $rombColorHoverText;
        }
    }
    &__description {
        background-color: #ccc;
        width: 360px;
        text-align: center;
        background-image: url("../img/fon-blue.jpg");
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 2px 0 0 0;
        position: relative;
        border-bottom-left-radius: $borderRadiusBlock;
        border-bottom-right-radius: $borderRadiusBlock;
        h4 {
            font-size: 24px;
            font-family: $mainFont;
            color: $colorTitleDes;
            text-transform: uppercase;
            padding-top: 50px;
            font-weight: bold;
        }
        p {
            font-size: 13px;
            font-family: $secondFont;
            color: $colorDes;
            padding: 20px 0 28px;
            line-height: 20px;
            width: 300px;
            text-align: center;
        }
    }
    &__romb {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        top: -40px;
    }
    &__romb span {
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 30px;
        font-family: $mainFont;
        font-weight: bold;
        color: $rombColorText;
        margin: 20px 0;
        width: $romb;
        height: $romb / 2;
        background: $rombColor;
        position: relative;
        box-shadow: 0 0 7px #000000;
        &:before , &:after {
            content: "";
            width: 0;
            height: 0;
            position: absolute;
            left: 0;
            border-left: $romb / 2 solid transparent;
            border-right: $romb / 2 solid transparent;
        }
        &:before {
            top: -$romb / 4;
            border-bottom: $romb / 4 solid $rombColor;
        }
        &:after {
            bottom: -$romb / 4;
            border-top: $romb / 4 solid $rombColor;
        }
    }
}

